<template>
  <v-container>
     <loading :active.sync="isLoading" 
        
        :is-full-page="fullPage"></loading>
    <v-row>
      <v-col cols="12">
        <label>Dashboard</label>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" sm="12">
        <div>
          <v-row>
            <v-col cols="12" sm="6" md="4" v-for="(course,index) in courseList" :key="index">
            <v-card router :to="{name:'overview',params:{id:course.id}}">

              <v-card-text>
                <div><img :src="'http://localhost/youtube/api/'+course.image" alt="" srcset="" width="100px" height="80px"></div>
                 <div class="font-weight-bold">{{course.title}}</div>
              </v-card-text>
            </v-card>
            </v-col>

          </v-row>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
export default {
  components:{
      Loading

  },
  data(){
    return{
      courseList:'',
      isLoading:true,
      fullPage:true

      }
  },
  methods:{
    getcourse(){
      axios.post('/courselist.php').then(resp=>{
        this.courseList=resp.data;
        this.isLoading=false;
      }).catch(error=>{
        this.isLoading=false;
        alert(error);
      })
    }
  },
  mounted(){
    this.getcourse();
  }
}
</script>
