export default {
    namespaced: true,
    state: {
        info: null
    },
    getters: {
        userInfo(state) {
            return state.info
        }
    },
    mutations: {
        setInfo(state, payl) {
            state.info = payl
        }
    },
    actions: {
        handleInfo({ commit }, payloa) {
            commit('setInfo', payloa);
        }
    }


}