import Vue from "vue"
import Router from "vue-router"
import notfound from './../components/404.vue'
import login from "./../components/login.vue"
import admin from './../views/admin/dashboard.vue'
import adminCourses from './../views/admin/courses.vue'
import adminCoursesview from './../views/admin/coursesview.vue'
import lessons from './../views/admin/lessons.vue'
//
import student from './../views/student/dashboard.vue'
import overview from './../views/student/overview.vue'
import studentcourse from './../views/student/studentcourse.vue'
import read from './../views/student/read.vue'
import NProgress from "nprogress"
import "nprogress/nprogress.css"
const routes = [

    { path: "/muhire", component: login, name: "login" },
    { path: "/admin/dashboard", component: admin, name: "admin" },
    { path: "/admin/courses", component: adminCourses, name: "adminCourses" },
    { path: "/admin/courses/:id/:name", component: adminCoursesview, name: "adminCoursesview" },
    { path: "/admin/courses/lessons/:id", component: lessons, name: "lessons" },
    //
    { path: "/", component: student, name: "student" },
    { path: "/user/dashboard/:id", component: overview, name: "overview" },
    { path: "/user/mycourse", component: studentcourse, name: "studentcourse" },
    { path: "/user/dashboard/:lesson/read", component: read, name: "read" },
    { path: "*", component: notfound, name: "notfound" },

]

Vue.use(Router)
const router = new Router({
        mode: "hash",
        linkActiveClass: "active",
        routes: routes
    })
    // router gards
router.beforeEach((to, from, next) => {
    NProgress.start()
        //auth route is authenticated
    next()
})

router.afterEach((to, from) => {
    NProgress.done()
})
export default router