<template>
  <div>
    <v-row>
      <v-col cols="12">
        <div>
          <label>Lesson: - {{$route.params.name}}</label>
          <v-spacer></v-spacer>
          <v-btn text color="red">Delete Lesson</v-btn>
          <v-btn text color="green" @click="savechanges()">Save Changes</v-btn>
        </div>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="11">
        <div class="main-wrapper ml-3">
          <v-row>
            <div>Content:</div>
          </v-row>
          <v-row>
            <v-col cols="12">
               <quill-editor
    ref="myQuillEditor"
    v-model="content"
    :options="editorOption"
  />
            </v-col>
          </v-row>
        </div>

      </v-col>
    </v-row>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  data(){
    return{
      content:"",
      lesson:""

    }
  },
  methods:{
    savechanges(){
      let myform =new FormData();
        myform.append('content',this.content);
        myform.append('lesson',this.lesson);
        axios.post('/lessonupdate.php',myform).then(resp=>{

        }).catch(error=>{

        })
    }
  },


  created(){
        let lesson=this.$route.params.id;
        this.lesson=lesson;
        let myform =new FormData();
        myform.append('lesson',lesson);
        axios.post('/lessonEditor.php',myform).then(resp=>{
          this.content=resp.data.content;
        }).catch(error=>{

        })
  }
}
</script>
