<template>
  <div>
    <v-row>
      <v-col cols="12">
        <div>
          <label>Courses - {{$route.params.name}}</label>
        </div>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="11">
        <div class="main-wrapper ml-3">
          <v-row>
            <div>Lessons:</div>
            <div>
              <v-row>
                <v-col cols="12" md="3"  sm="4"  color="secondary
                "  v-for="(lesson,index) in lessons_list" :key="index">
                  <div>
                   <v-card router :to="{name:'lessons',params:{id:lesson.id}}">
                     <v-card-text>

                        {{lesson.title}}
                     </v-card-text>
                     <v-card-actions>
                       {{lesson.topic}}
                     </v-card-actions>
                   </v-card>
                  </div>
                </v-col>
                <v-col cols="12" md="3">
                  <div>
                    <v-btn @click="dialog=true" text color="warning">Add  New Lesson</v-btn>
                  </div>
                </v-col>
              </v-row>
            </div>

          </v-row>
        </div>
        <v-dialog v-model="dialog" max-width="400">
          <v-card>
            <v-card-title></v-card-title>
            <v-card-text>
              <label class="font-weight">Add New Lesson</label>
              <v-form class="mt-1">
                  <v-text-field label="Lesson #" placeholder="1" v-model="lesson.id"></v-text-field>
                  <v-text-field label="Topic" placeholder="Introduction to Html5" v-model="lesson.topic"></v-text-field>
                  <v-btn text color="primary" :loading="loading"  @click="addlesson()">Finish</v-btn>
                  <v-btn text color="warning" @click="dialog=false">Close</v-btn>
              </v-form>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  data(){
    return{
      dialog:false,
      loading:false,
      error:false,
      lessons_list:[],
      lesson:{
        id:'',
        topic:''
      }

    }
  },
  methods:{
    addlesson(){
      this.loading=true;
      let myform=new FormData();
      myform.append('id',this.lesson.id);
      myform.append('topic',this.lesson.topic);
      myform.append('course',this.lesson.course);
      axios.post("/addlesson.php",myform).then(success=>{
        let resp=success.data.message;
        this.loading=false;
          this.dialog=false;
        if(resp=='ok'){
          this.lessons_list=[];
          this.lessons_list=success.data.lessons;
        }else{
          this.error=true;
        }
      }).catch(error=>{
         this.loading=false;
          this.dialog=false;
          this.error=true;
      });
    }
  },


  created(){
        this.lesson.course=this.$route.params.id;
        let myform=new FormData();
        myform.append('course',this.lesson.course);
          axios.post("/lessons.php",myform).then(success=>{
        let resp=success.data.message;
        this.loading=false;
          this.dialog=false;
        if(resp=='ok'){
          this.lessons_list=[];
          this.lessons_list=success.data.lessons;
        }else{
          this.error=true;
        }
      }).catch(error=>{
         this.loading=false;
          this.dialog=false;
          this.error=true;
      });
  }
}
</script>
