<template>
  <v-container>
    <Loading :active.sync="isLoading" 
        
        :is-full-page="fullPage"></Loading>
    <v-row>
      <v-col cols="12">
        <label v-if="isLoading==false">Dashboard - {{ courseList[0].title }} </label>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" sm="12">
        <div>
          <v-row>
            <v-col
              cols="12"
              sm="8"
              v-for="(course, index) in courseList"
              :key="index"
            >
              <v-card outlined>
                <v-card-text>
                  <div class="font-weight-bold">
                    <span>{{ course.title }}</span>
                  </div>

                  <div class="mt-2">
                    {{ course.details }}
                  </div>
                </v-card-text>
              </v-card>
            </v-col>

            <v-col
              cols="12"
              sm="4"

            >
              <v-card outlined>
                <v-card-text>
                  <div class="font-weight-bold">
                    <span>Course Modules</span>
                  </div>

                  <div class="mt-2">
                      <v-list>
                        <v-list-item v-for="(lesson,index) in modules" :key="index" router :to="{name:'read',params:{lesson:lesson.id}}">
                          <v-list-item-title>{{lesson.title}}</v-list-item-title>
                        </v-list-item>

                      </v-list>
                  </div>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
export default {
  components:{
    Loading
  },
  data() {
    return {
      courseList: "",
      modules: "",
      fullPage:true,
      isLoading:true
    };
  },
  methods: {
    getcourse() {
      let myform = new FormData();
      myform.append("id", this.course);
      axios
        .post("/courseoverview.php", myform)
        .then(resp => {
          this.courseList = resp.data.course;
          this.modules = resp.data[0].lessons;
          this.isLoading=false;
        })
        .catch(error => {
          alert(error);
          this.isLoading=false;
        });
    },
    subscribe() {
      let myform = new FormData();
      myform.append("id", this.course);
      axios
        .post("/subscribe.php", myform)
        .then(resp => {
          const data = resp.data.message;
          if (data == "ok") {
            alert("ok");
          } else {
          }
        })
        .catch(error => {
          alert(error);
        });
    },

  },
  mounted() {
    this.course = this.$route.params.id;
    this.getcourse();
  }
};
</script>
