import vue from 'vue'
import vuex from 'vuex'
import user from './user'
vue.use(vuex)

export default new vuex.Store({
    state: {
        loggedin: true,
        logged: "student",
    },
    getters: {
        logState(state) {
            return state.loggedin
        },
        status(state) {
            return state.logged
        }
    },
    mutations: {
        login(state) {
            state.logged = "admin";

        }
    },
    actions: {
        handlelogin({ commit }, d) {

            commit("login");
        }
    },
    modules: {
        user
    }
})