<template>
  <v-app>
    <toolbar></toolbar>
    <v-content class="mx-3">
      <headers></headers>
      <router-view></router-view>
    </v-content>
  </v-app>
</template>

<script>
import toolbar from './components/navigation.vue'
import headers from './components/headers.vue'
import { mapGetters } from 'vuex'
export default {
  components:{
    toolbar,
    headers
  },
  computed:{
    ...mapGetters({
      loggedin:"logState"
    })
  },
  watch:{
    loggedin(x){
      this.loggedin=x;


    }
  }

}
</script>
