<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <label>My courses</label>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" sm="12">
        <div>
          <v-row>
            <v-col cols="12" v-if="failed">
              <h3>Not Sub scribed to Any course click <v-btn   text color="success">Here To subscribe</v-btn></h3>
            </v-col>
            <v-col cols="12" sm="6" md="4" v-for="(course,index) in courseList" :key="index">
            <v-card router :to="{name:'overview',params:{id:course.id}}">

              <v-card-text>
                <div><img :src="'http://localhost/youtube/api/'+course.image" alt="" srcset="" width="100px" height="80px"></div>
                 <div class="font-weight-bold">{{course.title}}</div>
              </v-card-text>
            </v-card>

            </v-col>

          </v-row>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios'
export default {
  data(){
    return{
      courseList:'',
      course:'',
      failed:''

      }
  },
  methods:{
    getcourse(){
      let myform=new FormData();
      myform.append('id',this.course);
      axios.post('/mycourses.php',myform).then(resp=>{
        const data=resp.data.message;
        if(data==400){
          this.failed=true
        }else{

        }
      }).catch(error=>{
        alert(error);
      })
    }
  },
  mounted(){
    this.course=this.$route.params.id;
    this.getcourse();
  }
}
</script>
