<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md4 lg4>
        <v-card class="elevation-1 pa-3">
          <v-card-text>
            <div class="layout column align-center">
              <img src="/static/m.png" width="140" height="140" />
            </div>
            <VAlert type="error" :value="error.state">
              {{ error.text }}
            </VAlert>
            <v-form>
              <v-text-field
                append-icon="person"
                name="login"
                label="Login"
                type="text"
                v-model="model.username"
              ></v-text-field>
              <v-text-field
                append-icon="lock"
                name="password"
                label="Password"
                id="password"
                type="password"
                v-model="model.password"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <div class="login-btn">
            <v-btn block color="primary" @click="login" :loading="loading"
              >Login</v-btn
            >
          </div>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
import { mapActions } from "vuex";
import { mapGetters } from "vuex";
export default {
  data: () => ({
    loading: false,
    model: {
      username: null,
      password: null
    },
    error: {
      state: false,
      text: null
    }
  }),
  computed: {
    ...mapGetters({
      getInfo: "user/userInfo"
    })
  },
  watch: {
    getInfo(x) {}
  },
  methods: {
    ...mapActions({
      setInfo: "user/handleInfo"
    }),
    ...mapActions({
      log: "handlelogin"
    }),
    login() {
      this.error.state = false;
      this.loading = true;
      this.$axios
        .post("/login.php", {
          userInfo: this.model
        })
        .then(success => {
          if (success.data.failed == "failed") {
            this.loading = false;
            this.error.state = true;
            this.error.text = "Invalid Credintials";
          } else {
            this.setInfo(success.data);
            this.log(this.setInfo(success.data));
            const data = success.data.role;
            this.$router.push({ name: data });
            localStorage.setItem("heirTutorials", success.data.token);
          }
        })
        .catch(error => {
          this.loading = false;
          this.error.state = true;
          this.error.text = error;
        });
    }
  }
};
</script>
<style scoped lang="css">
.split-bg {
  height: 50%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  z-index: 0;
}
</style>
