<template>
  <div>
    <v-row>
      <v-col cols="12">
        <div>
          <label>Courses</label>
        </div>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="11">
        <div class="main-wrapper ml-3">
          <v-row>
            <v-col cols="6" md="2" v-for="(course,index) in courseList" :key="index">
              <v-card outlined router :to="{name:'adminCoursesview',params:{id:course.id,name:course.title}}">
                <v-card-text >
                  {{course.title}}
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </div>
        <div class="add_btn">
          <v-btn fab bottom fixed right color="primary" @click="dialog=true">
            <v-icon>create</v-icon>
          </v-btn>
        </div>
        <v-dialog max-width="600" persistent v-if="dialog" v-model="dialog" >
          <v-card class="p-2">
            <v-card-title>
              <v-spacer></v-spacer>
              <v-btn text color="error" @click="dialog=false">Close</v-btn>
            </v-card-title>
            <v-card-text>
              <label>Add New Courses/Subject</label>
              <div class="mt-2">
                <v-form>
                  <v-text-field label="Title" dense v-model="courses.title"></v-text-field>
                  <v-select v-model="courses.skills" chips label="required Skills" multiple :items="skills"></v-select>
                   <input type="file" ref="file" @change="handleupload()">
                   <v-textarea v-model="courses.details"></v-textarea>
                   <v-btn  color="primary" @click="addcourse" :loading="loading">Add course</v-btn>

                </v-form>
              </div>
            </v-card-text>



          </v-card>
        </v-dialog>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  data(){
    return{
      dialog:false,
      skills:['javascript','html','php','css'],
      courseList:null,
      loading:false,
      courses:{
        skills:[],
        title:null,
        details:null,
        file:''


      },

    }
  },
  methods:{
    addcourse(){
      let formData=new FormData();
      formData.append('file',this.courses.file);
      formData.append('skills',this.courses.skills);
      formData.append('title',this.courses.title);
      formData.append('details',this.courses.details);
      this.loading=true;
      axios.post("/addcourse.php",formData,{
        headers:{
          'Content-Type':'multipart/form-data'
        }
      }).then(success=>{
          const msg=success.data.message;
          if(msg=='ok'){
              this.loading=false;
              this.dialog=false;

          }else{

          }
      }).catch(error=>{

      });

    },
    handleupload(){
      this.courses.file=this.$refs.file.files[0];
    }
  },
  created(){
    axios.post("/courselist.php").then(success=>{
        this.courseList=success.data;
    }).catch(error=>{

    })
  }
}
</script>
